* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list__item {
  display: flex;
  align-items: center;
  padding: 10px;
  border-bottom: 1px solid #ccc;
}

.country-list__flag {
  margin-right: 10px;
}

.country-list__name {
  margin: 0;
  font-size: 1.2rem;
}

.country-info__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.country-info__item {
  margin-bottom: 10px;
}

.country-info__item b {
  font-weight: bold;
}

.country-info__item p {
  margin: 0;
  font-size: 1.2rem;
}
